.reviews {
    
    
    
    min-width: $mobile-width;
    margin: 0 auto;
    margin-top: 65px;
    background-image: none;


    @media (min-width: $desktop-width) {

            width: 91%;
            min-width: $desktop-container;
            max-width: $desktop-container;
            background-image: url('../img/review-background--350_457.jpg');
            background-repeat: no-repeat;
    
            
    }       
}

.reviews__wrapper {
    position: relative;
    
   
    width: 81%;
    margin: 0 auto;
    margin-bottom: 40px;


    @media (min-width: $desktop-width) {

   

            /* width: 76%;  */// 875 / 1150
            min-height: 457px;
            margin: 0;
            margin-bottom: 68px;
            margin-left: 175px;
            margin-top: 75px;
            
        }
}

.reviews__title {
    margin: 0;
    padding: 0;
    margin-bottom: 35px;

    font-size: 25px;
    line-height: 30px;
    font-weight: bold;
}

.reviews__item {
    margin: 0;
    padding: 0;
}

.reviews__text {
    margin: 0;
    padding: 0;
    margin-bottom: 35px;

    font-size: 15px;
    line-height: 30px;
    font-weight: 400;
    color: $dark-grey;
}

.reviews__author-name {
    font-family: initial;
    font-size: 17px;
    line-height: 24px;
    font-weight: bold;

    margin-bottom: 35px;
  
}

.reviews__social-contact{
    font-size: 17px;
    line-height: 30px;
    font-weight: 400;
    color: $dark-grey;

}

.reviews__toggles  {
    padding-top: 35px;
    @media (min-width: $tablet-width) and (max-width: $desktop-width) {
        padding-top: 0;
    }

    @media (min-width: $desktop-width) {
        padding-top: 0; 
    }
}

.reviews__write-review-button {
    margin-top: 38px;

    font-size: 18px;
        line-height: 20px;
        font-weight: bold;
}

@media (min-width: $tablet-width) and (max-width: $desktop-width) {
    .reviews {
        min-width: $tablet-width;

        
        background-image: url('../img/review-background--350_457.jpg');
        background-repeat: no-repeat;

    }

    .reviews__wrapper {
        width: 86%; 
        min-height: 457px;
        margin-bottom: get-vw(68px);
        margin-top: 75px;
    }

    .reviews__title {
        display: inline-block;
        margin-top: 75px;
        font-size: 43px;
        line-height: 50px;
    }

    .reviews__text {   
        font-size: 17px;      
    }

    .reviews__write-review-button {
        margin-top: 75px;
        width: 175px;
        position: absolute;
        top: 0;
        right: 0;
    }
}


@media (min-width: $desktop-width) {

   
 
    .reviews__title {
        display: inline-block;
        margin-top: 75px;
        font-size: 53px;
        line-height: 57px;
    }

    .reviews__text {   
        font-size: 17px;      
    }

    .reviews__write-review-button {
        margin-top: 75px;
        width: 175px;
        position: absolute;
        top: 0;
        right: 0;

    }

}